import { CommonModule } from '@angular/common';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, NgModule, OnDestroy, OnInit } from '@angular/core';
import { interval, Subscription } from 'rxjs';
import { tap } from 'rxjs/operators';

import { AutofocusModule } from './autofocus.directive';
import { PlatformService } from './platform.service';

@Component({
  selector: "app-slider",
  template: `
    <div class="card-container">
      <div
        *ngFor="let slide of slides; let index = index"
        class="card"
        role="button"
        tabindex="0"
        [attr.aria-pressed]="index === activeIndex ? true : false"
        [autofocus]="index === activeIndex ? true : false"
        (click)="setActive(index)"
      >
        <span class="icon">{{ slide.icon }}</span>
        <p>{{ slide.description }}</p>
      </div>
    </div>

    <div class="details">
      {{ slides[activeIndex].detail }}
    </div>
  `,
  styles: [
    `
      :host {
        display: block;
      }

      .card-container {
        display: flex;
        justify-content: center;
      }

      .card:first-child {
        margin-left: 0;
      }

      .card:last-child {
        margin-right: 0;
      }

      .card {
        display: flex;
        flex-direction: column;
        flex-basis: 180px;
        text-align: center;
        cursor: pointer;
      }

      .icon {
        display: block;
        margin-bottom: 8px;
        text-align: center;
        font-size: 22px;
      }

      .card:focus {
        transform: translateY(-3px);
        background: #fff;
        box-shadow: 0 2px 10px rgb(0 0 0 / 11%);
        outline: none;
      }

      .details {
        margin: 20px 0;
        font-size: 1.4rem;
        font-style: italic;
      }
    `,
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SliderComponent implements OnInit, OnDestroy {
  slides = [
    {
      value: "discussions",
      description: "Discussions libres.",
      detail:
        "Des discussions libres pour les gurus, les rookies ou simplement les curieux.",
      icon: "üë•",
    },
    {
      value: "talks",
      description: "Talks sur des sujets associ√©s √† Angular.",
      detail: "Des talks √† propos d'Angular et son √©cosyst√®me.",
      icon: "üó£",
    },
    {
      value: "workshops",
      description: "Workshops autour d'Angular.",
      detail:
        "La pratique d'un sujet en particulier au travers de travaux guid√©s.",
      icon: "üõ†",
    },
    {
      value: "bring-your-problem",
      description: "Ram√®ne ton probl√®me.",
      detail:
        "On r√©fl√©chi √† une solution ensemble, puis on propose un workshop pour le r√©soudre.",
      icon: "üêû",
    },
  ];

  activeIndex = 0;

  timer$ = interval(5_000).pipe(
    tap(() => {
      this.setActive(
        this.activeIndex + 1 > this.slides.length - 1 ? 0 : this.activeIndex + 1
      );
      this.cd.detectChanges();
    })
  );

  slideSub: Subscription;

  constructor(
    private cd: ChangeDetectorRef,
    private platform: PlatformService
  ) {}

  ngOnInit(): void {
    if (this.platform.isPlatformBrowser()) {
      this.timer$.subscribe();
    }
  }

  ngOnDestroy(): void {
    if (this.platform.isPlatformBrowser()) {
      this.slideSub && this.slideSub.unsubscribe();
    }
  }

  setActive(index: number): void {
    this.activeIndex = index;
    this.slideSub && this.slideSub.unsubscribe();
    this.slideSub = this.timer$.subscribe();
  }
}

@NgModule({
  imports: [CommonModule, AutofocusModule],
  declarations: [SliderComponent],
  exports: [SliderComponent],
})
export class SliderModule {}
